:root {
  --primaryColor: #131313;
  --btn-hoverColor: #0a8817;
  --btn-disabledColor: #ababab;
  --backgroundColor: #84a178;
  --extrabackgroundColor: #fcfcfc;
  --accentColor: #f8d24e;
  --textColor: #fcfcfc;
  --extraColor: #d6936d;
  --copyRightColor: #7a7a7a;
  --borderColor: #e1e1e1;
  --transitionDuration: 250ms;
  --transitionTimingFunction: cubic-bezier(.4, 0, .2, 1);
}

.btn__transparent {
  max-width: 250px;
  min-width: 108px;
  min-height: 38px;
  color: #000;
  border: 2px solid var(--accentColor);
  cursor: pointer;
  transition: border-color var(--transitionDuration) var(--transitionTimingFunction);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 8px;
  padding: 8px 20px;
  font-family: inherit;
  font-size: 12px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.25;
  position: relative;
  overflow: hidden;
}

.btn__transparent:hover, .btn__transparent:focus {
  border-color: var(--btn-hoverColor);
}

.btn__transparent:focus {
  color: var(--extrabackgroundColor);
  outline: none;
}

.btn__transparent:disabled {
  color: var(--primaryColor);
  border-color: var(--btn-disabledColor);
  cursor: initial;
  outline: none;
}

@media screen and (min-width: 1200px) {
